.alpheios-popup-lexdataloading__progress-wrapper {
  margin: 0 1rem 2rem !important;
  box-sizing: border-box !important;
}
.alpheios-popup-lexdataloading__progress-inner {
  border: 2px solid #73CDDE !important;
  text-align: center !important;
  box-sizing: border-box !important;
  position: relative !important;
  background: white !important;
}
.alpheios-popup-lexdataloading__progress-line {
  -webkit-animation: progress 3s infinite;
  position: absolute !important;
  left: 0 !important;
  top: 0 !important;
  height: 100% !important;
  width: 100% !important;
}
.alpheios-popup-lexdataloading__progress-text {
  display: block;
  padding: 4px !important;
  box-sizing: border-box !important;
  text-transform: uppercase !important;
  position: relative !important;
  z-index: 10 !important;
  border: 2px solid #fff !important;
  font-size: 85% !important;
}
@-webkit-keyframes progress {
0% {
    background: #b1e3ed;
    width: 0%;
}
100% {
    background: #73CDDE;
    width: 100%;
}
}
@keyframes progress {
0% {
    background: #b1e3ed;
    width: 0%;
}
100% {
    background: #73CDDE;
    width: 100%;
}
}
@-o-keyframes progress {
0% {
    background: #b1e3ed;
    width: 0%;
}
100% {
    background: #73CDDE;
    width: 100%;
}
}
@-ms-keyframes progress {
0% {
    background: #b1e3ed;
    width: 0%;
}
100% {
    background: #73CDDE;
    width: 100%;
}
}
@-moz-keyframes progress {
0% {
    background: #b1e3ed;
    width: 0%;
}
100% {
    background: #73CDDE;
    width: 100%;
}
}